import React, { useState , useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { postVerificarToken } from '../../services/authService'
import AuthContext from './authContext'


const AuthState = ({children}) => {
    
    const history = useHistory();



    const [ auth , setAuth ] = useState({
        autenticado : false ,
        usu_nom : null  ,
        usu_id : null ,
        token : null,
        cargando : true 
    })    

    const cerrarSesion = () =>{
        localStorage.removeItem("token");
        //push vs replace ()
        //push() nos da la posibilidad de navegar a una nueva ruta, sin embargo 
        //podemos clicar en el boton ATRAS del navegador y podemos tener problemas.
        //replace() mismo comportamiento pero borra el historial de navegacion 
        //en nuestro proyecto 
        history.replace("/auth/login");
        setAuth({
            autenticado : false ,
            usu_nom : null  ,
            usu_id : null ,
            token : null,
            cargando : true 
        })

    }

   const iniciarSesionContext = token  =>{
    //guardamos el token en el local storage por que al actualizar
    //la pagina el local storage no se borra

    localStorage.setItem("token", token);

    const payload = token.split(".")[1];
    console.log(payload);
    //atob desencriptan solo en base64
   //btoa encriptan solo en base64
    const payloadDescriptado = atob(payload)
    const payloadJSON = JSON.parse(payloadDescriptado);
    console.log (payloadJSON);
    setAuth({
        autenticado: true ,
        usu_nom : payloadJSON.usu_nom,
        usu_id : payloadJSON.usu_id ,
        token : token ,
        cargando : false 
    });
   }     

   const iniciarSesionConLocalStorage =() =>{
        const token = localStorage.getItem("token");
        if(token){
            //verificar si es valido atravez del backend
            postVerificarToken(token).then( data => {
                console.log(data);
                if(data .ok ) {
                    const payload = token.split(".")[1];
                    const payloadDescriptado = window.atob(payload) ;
                    const payloadJSON = JSON.parse(payloadDescriptado);
                    setAuth({
                        autenticado: true ,
                        usu_nom : payloadJSON.usu_nom ,
                        usu_id : payloadJSON.usu_id ,
                        token : token ,
                        cargando : false  
                    })
                                    
                }else
                {
                    localStorage.removeItem("token")
                    setAuth({
                        autenticado: false,
                        usu_nom : null ,
                        usu_id : null ,
                        token : null ,
                        cargando : true 
                    })
                    //redireccionar al home 
                }
           })
        }else
        {
            setAuth({
                autenticado: false,
                usu_nom : null ,
                usu_id : null ,
                token : null ,
                cargando : true 
            })
            //redireccionar al home 

        }

   }
   useEffect(() => {
    iniciarSesionConLocalStorage();
   }, [])


    return (
        <AuthContext.Provider value={{
            // ...auth
            autenticado:auth.autenticado,
            usu_nom: auth.usu_nom ,
            usu_id : auth.usu_id ,
            token : auth.token,
            cargando : auth.cargando ,
            iniciarSesionContext,
            cerrarSesion : cerrarSesion
        }}>
            {children}
        </AuthContext.Provider>
        )
}

export default AuthState
