import React , { useState , useContext } from 'react'
import AuthContext from '../../../../context/auth/authContext';
import { postLogin } from '../../../../services/authService';

const AuthLoginScreen = ( props ) => {

    const {iniciarSesionContext} = useContext (AuthContext);
    const [ formulario , setFormulario ]  =useState(
        {
            correo:"",
            password:""
        }
    )

    const handleChange = e => {
        setFormulario({
            ...formulario,
            [e.target.name] : e.target.value 
        })
    }
        const submit = e =>{
            e.preventDefault();
            postLogin(formulario).then(rpta =>{
                console.log(rpta);
                if (rpta.ok){
                    //significa login exitoso =) 
                    iniciarSesionContext(rpta.token) ;
                    props.history.push("/pos/pedido")
                }
            })
            // Todo : Apliocar Validacion 
        }
    
    return (
        <main className="container-fluid bg-dark login-screen">
            <div className="row">
                <div className="col-md-4">
                <div className="card">
                <div className="card-body">
                    <form action="" onSubmit= {submit}>
                        <div className="form-group">
                            <label htmlFor="">Usuario</label>
                            <input type="text" placeholder="irupay@gmail.com"
                            className="form-control"
                            onChange={handleChange} value ={formulario.correo}
                            id="correo" 
                            name="correo"/>

                        </div>
                        <div className="form-group">
                            <label htmlFor="">Password</label>
                            <input type="password" 
                            className="form-control"
                            onChange={handleChange} value = {formulario.password}
                            id="password" 
                            name="password"/>
                        </div>
                        <div className="form-group">
                            <button type="submit" className="btn btn-dark">
                                Iniciar Sesión
                            </button>
                        </div>
                    </form>

                </div>
            </div>
                     
                </div>
            </div>
       </main>
    )
}

export default AuthLoginScreen
